.app-wrapper {
  display: grid;
  width: 1500px;
  margin: 0 auto;
  grid-template-areas: 
 "header header"
  "nav main";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 20px;
}


.app-wrapper-content {
  grid-area: main;
  background-color: lightsteelblue;
}

.app-nav {
  background-color: lightgreen;

}